/../style/all\";\n\n* {\n    box-sizing: border-box;\n}\n\n.menu-toggle-button {\n    border: none;\n    background: transparent;\n    background-color: rgb(125, 116, 109);\n    border-radius: 2px;\n    color: rgb(255, 255, 255);\n    cursor: pointer;\n    width: 45px;\n    height: 37px;\n    @include breakpoint(laptop) {\n        width: 95px;\n        background-color: rgb(125, 116, 109);\n        color: #fff;\n    }\n    &.open {\n        background-color: #414042;\n    }\n    span {\n        font-size: 1.2em;\n        font-weight: 700;\n        padding: 2px;\n        vertical-align: middle;\n        &.menu-title {\n            display: none;\n            @include breakpoint(laptop) {\n                display: inline-block;\n            }\n        }\n    }\n    svg {\n        height: 29px;\n        width: 29px;\n        padding: 2px;\n        vertical-align: middle;\n    }\n}\n.menu-wrapper {\n    position: absolute;\n    left: 0;\n    width: 100%;\n    &.static-position{\n    }\n\n    .menu-container {\n        @include border-radius(0 0 3px 3px);\n        box-sizing: border-box;\n        overflow: hidden;\n        transition: all 0.3s ease-in-out;\n        max-height: 0;\n        width: 100%;\n        background-color: #dfdcda;\n        margin: auto;\n        position: relative;\n        top: 14px;\n\n        @include breakpoint(tablet) {\n            width: 750px;\n        }\n        @include breakpoint(laptop) {\n            width: 970px;\n        }\n        @include breakpoint(desktop) {\n            width: 1170px;\n        }\n        @media print {\n            display: none;\n        }\n\n        &.open {\n            @include box-shadow(0 5px 5px -2px rgba(0, 0, 0, 0.26));\n            height: auto;\n            max-height: 300px;\n            overflow: auto;\n        }\n        .menu-item-list-container {\n            padding: 15px;\n            .menuItemList {\n                list-style: none;\n                margin: 0;\n                color: #414042;